html,
body {
  height: 100%;
  overflow-y: hidden;
}
.rrbe {
  display: flex;
  flex-direction: row;
  height: 100vh;
  &__left {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-basis: 30%;
    max-width: 400px;
    color: #242424;
    background-color: #e6e9ec;
    &-top {
      height: 40px;
      background-color: #343b4c;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      h4 {
        font-size: 1rem;
        text-align: center;
        margin-top: 13px;
        color: #fff;
      }
    }
    &-bottom {
      height: 60px;
      background-color: #343b4c;
      .bottom {
        padding: 15px;
        display: flex;
        justify-content: space-between;
      }
    }
    &-container {
      height: 100%;
      overflow: auto;
      -ms-overflow-style: none;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .accordion {
      border-radius: 0.25rem;
      .card {
        &-header {
          background-color: #ffffff;
          color: #343b4c;
        }
        &-body {
          background-color: #ffffff;
        }
      }
    }
    .card {
      border: 0;
      &-header {
        padding: 0.5rem 1.25rem;
        font-size: 0.9rem;
        color: #343b4c;
        cursor: pointer;
      }
      &-body {
        color: #343b4c;
        .card-header-pills {
          margin-bottom: 20px;
          font-size: 0.85rem;
        }
        .form-label,
        .form-check-label {
          font-size: 0.9rem;
        }
      }
    }
  }
  &__resizer {
    width: 5px;
    height: 100vh;
    cursor: col-resize;
    background-color: #e6e9ec;
  }
  &__right {
    width: 100%;
    &-container {
      overflow: auto;
      height: 100vh;
    }
  }
  &__section {
    &:hover {
      background-color: #fafafa;
    }
  }
  &__column {
    &:hover {
      background-color: rgba(238, 238, 238, 0.5);
    }
  }
}
