.settings {
  .btn-group,
  .btn-group-vertical {
    width: 100%;
    .btn {
      border-radius: 0;
      background-color: #fff;
      color: #242424;
      border-color: #fff;
      font-size: 0.81rem;
      &.active:not(:disabled) {
        background-color: #f7f8f9;
        border-color: #fff;
        border-bottom: 3px solid #343b4c;
        color: #242424;
      }
      &.focus {
        box-shadow: none;
      }
    }
  }
  &__container {
    padding: 15px;
  }
}
